import React, { useState} from "react";
import { Card } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import ImageCarouselPropertiesJson from "../../../Properties/ImageCarouselPropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import RedCross from "../../../../Icons/RedCross";
import SvgEditIcon from "../../../../Icons/EditIcon";
import DragAdvListview from "../../../../Icons/DragAdvListview";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import ImageEditor from "./ImageEditor";
import $ from "jquery";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import Tooltip from "../../../../ReusableComponents/Tooltip/Tooltip";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

const ImageCarouselProperties = (props) => {
  const translationStyles1 = window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";
  const contexts = GetStateFromData(props.containerId);

  const {
    updateJson,
    deleteControl,
    showAlertConfirmNotifications
  } = contexts.contexts;
  const { updatedComponentInFormState } = updateJson;

  const { t } = useTranslation(["styles", "translation"]);
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});
  const [draggedImagePosition, setDraggedImagePosition] = useState();
  const [editableImage, setEditableImage] = useState();
  const [imgInd, setImgInd] = useState(-1);

  function handleCombo(comboId) {
    var select = document.getElementById(comboId);
    var propertyName = select.getAttribute("name");
    var optionTag = select.options[select.selectedIndex];
    handleProperty(propertyName, optionTag.value);
  }

  function handleRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        !regexValidator(event.key, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage"
        });
      }
    } catch (e) {}
  }

  function handleProperty(propertyName, propertyValue, event, inputBoxId) {
    try {
      if (propertyName === "CustomId") {
        if (propertyValue !== props.selectedControl.style.CustomId) {
          var element = event.target;
          var jsonToSend = {
            customControlId: propertyValue,
            controlid: props.selectedControl.id
          };
          validateCustomControlId(jsonToSend)
            .then((response) => {
              if (response && response.data && response.data.status === "0") {
                let updatedJson = updateUserId(
                  props.selectedControl, // whole control Json
                  propertyValue //newly defined id
                );
                if (updatedJson) {
                  updatedComponentInFormState(props.selectedControl.id, updatedJson);
                }
              } else if (response && response.data && response.data.status === "-1") {
                showAlertConfirmNotifications({
                  open: true,
                  showConfirm: "showNotification",
                  alertType: "error",
                  alertContentKey: "CustomControl Id already Exist"
                });

                element.value = props.selectedControl.style.CustomId || "";
              }
            })
            .catch(function (error) {
              // handle error
              element.value = props.selectedControl.style.CustomId || "";
              console.log(error);

              showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: `Server side error ${error}`
              });
            });
        }
      }
      else {
        props.selectedControl.style[propertyName] = propertyValue;
        if (propertyName === "ImageCarouselHeight") {
          props.selectedControl.style["ImageCarouselWidth"] = propertyValue;
        }
        if (propertyName === "AutoplaySpeed") {
          let speed = props.selectedControl.style["Speed"];
          if (Number(propertyValue) < Number(speed)) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: `${t("translation:Autoplay speed should not be less than speed")}`
            });
          } else {
            props.selectedControl.style[propertyName] = propertyValue;
          }
        }
        if (propertyName === "Speed") {
          let autoPlaySpeed = props.selectedControl.style["AutoplaySpeed"];
          if (Number(propertyValue) > Number(autoPlaySpeed)) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: `${t("translation:Speed should not be greater than autoplay speed")}`
            });
          } else {
            props.selectedControl.style[propertyName] = propertyValue;
          }
        }
        updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
        props.applyChanges(props.formData);
        setErrorMessageId(false);
      }
    } catch (e) {}
  }

  function handleImageEditing(arg, imageFile, imgStyle) {
    if (arg === "Save2") {
      props.selectedControl.style.imageList[imgInd].imageStyle = imgStyle;
    } else if (arg === "Save") {
      props.selectedControl.style.imageList[imgInd].imageStyle = imgStyle;
      props.selectedControl.style.imageList[imgInd].imageFile = imageFile;
    }
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleOpenCrop(index) {
    setImgInd(index);
    setEditableImage(true);
  }

  function handleClose() {
    setEditableImage(false);
  }

  function handleIcon(imageName, imageFile) {
    if (!props.selectedControl.style["imageList"]) {
      props.selectedControl.style["imageList"] = [];
    }

    if (imageName !== "" && imageFile !== "")
      props.selectedControl.style["imageList"].push({
        imageName: imageName,
        imageFile: imageFile
      });
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    props.applyChanges(props.formData);
  }

  function handleNoIcon() {
    //customalert
    showAlertConfirmNotifications({
      open: true,
      showConfirm: "showNotification",
      alertType: "error",
      alertContentKey: "CANT_UPLOAD"
    });
    setTimeout(function () {
      $("#limitDiv").css("background-color", "red");
    }, 1);
  }

  function changeImage() {}

  function deleteImage(imageDetails, index) {
    try {
      var indexToBeDeleted = -1;
      let currControl = { ...props.selectedControl };
      props.selectedControl.style["imageList"].map((imgDetails, imgIndex) => {
        if (imgDetails.imageName === imageDetails.imageName) indexToBeDeleted = imgIndex;
      });
      if (indexToBeDeleted > -1) {
        currControl.style["imageList"].splice(indexToBeDeleted, 1);
        currControl.style["iconName"] = "";

        // WORK_ON_THIS
        updatedComponentInFormState(currControl.id, currControl);
      }
    } catch (e) {
      console.log("Error occured while deleting image from image carousel list");
    }
  }

  function allowDrop(event) {
    event.preventDefault();
  }

  function handleDrag(event) {
    setDraggedImagePosition(event.target.getAttribute("imagePosition"));
  }

  function handleDrop(event) {
    if (event === "undefined") {
      event.preventDefault();
    } else if (event.target.getAttribute("imagePosition")) {
      try {
        let targetPosition = event.target.getAttribute("imagePosition");
        let temp_imageDetails = props.selectedControl.style["imageList"];
        let draggedImage = temp_imageDetails[draggedImagePosition];
        temp_imageDetails[draggedImagePosition] = temp_imageDetails[targetPosition];
        temp_imageDetails[targetPosition] = draggedImage;
        props.selectedControl.style["imageList"] = temp_imageDetails;
        updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
        props.applyChanges(props.formData);
      } catch (e) {
        //customalert
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "INTERCHANGE_IMAGE_POSITION"
        });
      }
    }
  }

  if (ImageCarouselPropertiesJson && ControlsMap) {
    var CollapsibleItems = [];
    Object.keys(ImageCarouselPropertiesJson).forEach((key, index) => {
      var collapseComponentBody = [];
      Object.keys(ImageCarouselPropertiesJson[key]).forEach((property) => {
        if (ControlsMap[property] && ControlsMap[property].type) {
          if (props.selectedControl.columnList && property === "Column1") {
            props.selectedControl.columnList.map((col, index) => {
              if (col && col.style) {
                collapseComponentBody.push(
                  prepareHtmlTag(col.style.ColumnName, ControlsMap[property].type, col.columnIndex)
                );
              }
            });
          } else {
            collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
          }
        }
      });

      CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={key} body={collapseComponentBody} />);
    });
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    handleProperty(field, value);
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        passValue = [
          propertyValue.slice(0, event.target.selectionEnd) + event.key + propertyValue.slice(event.target.selectionEnd)
        ].join("");
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage"
        });
      } else {
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handlePropertyText(propertyName, propertyValue) {
    props.selectedControl.style[propertyName] = propertyValue;
    props.applyChanges(props.formData);
  }

  function prepareHtmlTag(key, type, colId) {
    switch (type.toLowerCase()) {
      case "combo":
        return (
          <div className={styles.parentDiv} style={translationStyles1 ? { display: "flex" } : null}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={styles.RightLabelSelectDiv1}>
              <select
                className={styles.RightLabelSelect1Table}
                name={key}
                id={props.selectedControl.id + key}
                onChange={() => handleCombo(props.selectedControl.id + key)}
                key={props.selectedControl.id}
              >
                {ControlsMap[key].children.map((optionTagData, optionCount) => {
                  if (ControlsMap[key].children[optionCount].Value) {
                    if (
                      props &&
                      props.selectedControl &&
                      props.selectedControl.style &&
                      props.selectedControl.style[key] === ControlsMap[key].children[optionCount].Value
                    ) {
                      return (
                        <option value={ControlsMap[key].children[optionCount].Value} selected>                          
                          {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                        </option>
                      );
                    } else {
                      return (
                        <option value={ControlsMap[key].children[optionCount].Value}>                          
                          {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                        </option>
                      );
                    }
                  }
                })}
              </select>
            </div>
          </div>
        );

      case "text_top_bottom":
        return (
          <>
            <span>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div>
              <input
                type="text"
                aria-label={"Control Value1"}
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                className={[
                  styles.fullWidthTextBox,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyPress={(event) => handleRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => handleRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </>
        );

      case "toggle":
        return (
          <div className={styles.toggleBtn}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
              <ToggleBtn
                key={props.selectedControl.id}
                toggleState={props.selectedControl.style[key]}
                handleToggle={handleToggle}
                field={key}
              />
            </div>
          </div>
        );

      case "text":
        return (
          <div className={styles.parentDiv} style={translationStyles1 ? { display: "flex" } : null}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
              {ControlsMap[key].label === "Autoplay Speed" ? (
                <Tooltip tooltip=
                {t(`translation:${"Autoplay speed should be greater than or equal to speed"}`)} />
              ) : null}
              {ControlsMap[key].label === "Speed" ? (
                <Tooltip tooltip={t(`translation:${"Speed should be less than or equal to autoplay speed"}`)} />
              ) : null}
            </span>
            <div className={styles.RightTextBox}>
              <input
                type="text"
                aria-label={"Control Value2"}
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                className={styles.RightLabelTextBox}
                onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
              {key !== "CustomId" ? (
                <div className={translationStyles1 ? styles.RightTextboxPlaceholderArabic : styles.RightTextboxPlaceholder}>{ControlsMap[key].unit}</div>
              ) : null}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );
      case "textbox":
        return (
          <div>
            <div className={styles.leftLabelForTextBox}>
              <div className={styles.labelForLabel}>{t(`translation:${ControlsMap[key].label}`)} </div>
              <input
                type="text"
                aria-label={"Control Value3"} 
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                maxLength={window?.appformBuilder_configs?.label_max_length}
                className={[
                  styles.textBoxLayoutForLable,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textarea":
        return (
          <div className={styles.parentDiv}>
            <p className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</p>

            <textarea
              aria-label={props.selectedControl.style[key]}
              className={[
                styles.textBoxLayoutStyletextArea,
                errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
              ].join(" ")}
              onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
              onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
              onBlur={(event) => handlePropertyText(key, event.target.value)}
              key={props.selectedControl.id}
            />

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "imageupload":
        return (
          <div className={styles.parentDiv}>
            <p className={styles.propertiesHeader1}>{t(`translation:${ControlsMap[key].label}`)}</p>
            {props.selectedControl &&
            props.selectedControl.style &&
            props.selectedControl.style["imageList"] &&
            props.selectedControl.style["imageList"].length >= 5 ? (
              <ImageUploadBtn
                containerId={props.containerId}
                handleImageUpload={handleNoIcon}
                iconName={""}
                selectedControl={props.selectedControl}
              />
            ) : (
              <ImageUploadBtn
                containerId={props.containerId}
                handleImageUpload={handleIcon}
                iconName={props.selectedControl.style.iconName || ""}
                selectedControl={props.selectedControl}
              />
            )}
            <div id="limitDiv" className={styles.imageCarouselinfo}>
              {t(`translation:${ControlsMap[key].info}`)}
            </div>

            {props.selectedControl &&
            props.selectedControl.style &&
            props.selectedControl.style["imageList"] &&
            props.selectedControl.style["imageList"].length > 0
              ? props.selectedControl.style["imageList"].map((imageDetails, index) => {
                  if (imageDetails) {
                    return (
                      <div
                        className={styles.imageCarouselImageEditDelete}
                        id={"imageDetails_" + index}
                        imagePosition={index}
                        onDragOver={allowDrop}
                        onDrop={(event) => handleDrop(event)}
                      >
                        <div
                          className={styles.addImageImageCarousel}
                          imagePosition={index}
                          draggable="true"
                          onDragStart={(event) => handleDrag(event)}
                        >
                          <span
                            id={"image_" + index}
                            className={styles.imageCarouselSpan}
                            draggable="true"
                            imagePosition={index}
                            onClick={() => changeImage(imageDetails, index)}
                          >
                            <DragAdvListview />
                          </span>
                          <span
                            className={[styles.imageCarouselSpan, styles.imageCarouselImageFileName].join(" ")}
                            draggable="true"
                            imagePosition={index}
                          >
                            {" "}
                            {imageDetails.imageName}{" "}
                          </span>
                          <span
                            id={"image_" + index}
                            className={[styles.imageCarouselSpan, styles.imageCarouselEditIcon].join(" ")}
                            onClick={() => handleOpenCrop(index)}
                          >
                            <SvgEditIcon />
                          </span>
                          <span
                            id={"image_" + index}
                            className={styles.imageCarouselSpan}
                            onClick={() => deleteImage(imageDetails, index)}
                          >
                            <RedCross />
                          </span>
                        </div>
                      </div>
                    );
                  }
                })
              : null}
          </div>
        );
    }
  }

  return (
    <>
      <ControlLabel
        label="Image Carousel"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId={props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        <Card.Body className={styles.cardBodyButtonProperties}>
          {CollapsibleItems.map((item, index) => {
            return item;
          })}
        </Card.Body>
      </div>
      {editableImage == true && props.selectedControl ? (
        <ImageEditor
          open={true}
          containerId={props.containerId}
          selectedControl={props.selectedControl}
          key={editableImage}
          imageDetails={{
            imageFile: props.selectedControl.style.imageList[imgInd].imageFile
          }}
          imageStyle={props.selectedControl.style.imageList[imgInd].imageStyle}
          handleImageEditing={(arg, imgDetails, imgStyle) => handleImageEditing(arg, imgDetails, imgStyle)}
          handleClose={handleClose}
        />
      ) : (
        <></>
      )}
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
    </>
  );
};

export default ImageCarouselProperties;
